@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.5.1/css/all.min.css");


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
body{
  background-color: #fffbd2;
  font-family:Georgia, 'Times New Roman', Times, serif;
}
.header{
  text-align: center;
}
.header img{
  height:20vh;
  width:30vw;
}

.section1{
  display:flex;
  
 
}
.trophy img{
   height:120vh;
   width:30vw;
}
.desc img{
  width:100%;
  height:80vh;
  
}
.desc ul li{
  text-align: justify;
}
.desc span.heading{
  text-align: center;
}
.section2{
  text-align: center;
}
.section2 img{
  width:70vw;
}
.section2 p{
  text-align: center;
}
hr{
  height:2px;
  background-color: #ec3237;
}
.lastSection{
  text-align: center;
  padding:20px;
}

.footer{
  color:white;
  background-color: #ec3237;
 padding:10px;

}

.footer ul{
  display:flex;
  align-items: center;
  justify-content: space-evenly;
}
.footer ul li{
  list-style: none;

}


@media screen and (max-width:600px) {
 
  .section1{
    flex-direction: column;
  
    
  }
  .trophy{
    text-align: center;
    
  }
  .trophy img{
    width:70vw;
    height:60vh;
  }
  .header img{
    width:90vw;
  }
  .desc img{
    height: 30vh;
  }
  .desc ul{
    padding:0px 30px;
  }
  .section2 img{
    width:80vw;
  }
  .lastSection span{
    font-size: 11px;
  }
  .footer ul{
   flex-direction: column;
   
  }
  .footer ul li{
    padding:5px 0px;
    margin-right:30px;
  }
 
  
}